(function($){
    'use strict';

    var $mobile_trigger = $('.hamburger'),
        $main_nav = $('.main-navigation');

        $mobile_trigger.on('click', function(e){
          $(this).toggleClass('is-active');
          $main_nav.toggleClass('main-navigation--open');
        });

})(jQuery);

(function($){
    'use strict';

    var $sidebar = $('.container__right'),
        $sidebar_nav = $('.main-navigation__item--sidebar');

        $sidebar_nav.on('click', function(e){
          $sidebar.toggleClass('container__right--open');
          e.preventDefault();
        });

})(jQuery);
